'use client';

import React, { useMemo, useState } from 'react';
import { useRouter } from 'next/navigation';
import catalogData from '@/data/catalog.json';
import { recordSearch } from '@/utils/searchHistory';
import dynamic from 'next/dynamic';
const ForYouFeather = dynamic(() => import('./ForYouFeather'), { ssr: false });
import { 
  PresentationChartLineIcon,
  TableCellsIcon,
  ChatBubbleLeftRightIcon,
  DocumentTextIcon,
  ChartBarIcon,
  CodeBracketIcon,
  PhotoIcon,
  VideoCameraIcon,
  MicrophoneIcon,
  SparklesIcon
} from '@heroicons/react/24/outline';

import aiAgentsData from '@/data/ai-agents.json';

type IconKey =
  | 'PresentationChartLineIcon'
  | 'TableCellsIcon'
  | 'ChatBubbleLeftRightIcon'
  | 'DocumentTextIcon'
  | 'ChartBarIcon'
  | 'CodeBracketIcon'
  | 'PhotoIcon'
  | 'VideoCameraIcon'
  | 'MicrophoneIcon'
  | 'SparklesIcon';

const ICONS = {
  PresentationChartLineIcon,
  TableCellsIcon,
  ChatBubbleLeftRightIcon,
  DocumentTextIcon,
  ChartBarIcon,
  CodeBracketIcon,
  PhotoIcon,
  VideoCameraIcon,
  MicrophoneIcon,
  SparklesIcon,
};

interface AIAgent {
  id: string;
  name: string;
  description: string;
  icon: React.ComponentType<{ className?: string }>;
  isNew?: boolean;
  isHot?: boolean;
}

type AIAgentJson = {
  id: string;
  name: string;
  description: string;
  icon: IconKey;
  isNew?: boolean;
  isHot?: boolean;
};

const aiAgents: AIAgent[] = (aiAgentsData as AIAgentJson[]).map((a) => ({
  id: a.id,
  name: a.name,
  description: a.description,
  icon: ICONS[a.icon] || SparklesIcon,
  isNew: a.isNew,
  isHot: a.isHot,
}));

export function MainContent() {
  const router = useRouter();
  type AppEntry = { id: string; title: string };
  const apps = catalogData as unknown as AppEntry[];
  const [query, setQuery] = useState('');
  const [active, setActive] = useState(0);
  const [listening, setListening] = useState(false);

  const normalize = (s: string) => s.toLowerCase().replace(/[^a-z0-9 ]/g, ' ').replace(/\s+/g, ' ').trim();
  const findBestApp = (input: string): AppEntry | undefined => {
    const q = normalize(input);
    if (!q) return undefined;
    let best: { score: number; app: AppEntry } | undefined;
    for (const a of apps) {
      const t = normalize(a.title);
      let score = 0;
      if (t === q) score = 100;
      else if (t.startsWith(q) || q.startsWith(t)) score = 80;
      else if (t.includes(q) || q.includes(t)) score = 60;
      else {
        const qt = new Set(q.split(' '));
        const tt = new Set(t.split(' '));
        let overlap = 0;
        qt.forEach(w => { if (tt.has(w)) overlap++; });
        if (overlap > 0) score = 40 + overlap;
      }
      if (!best || score > best.score) best = { score, app: a };
    }
    if (best && best.score >= 60) return best.app;
    return undefined;
  };

  const filtered = useMemo(() => {
    const q = query.trim().toLowerCase();
    if (!q) return [] as AppEntry[];
    return apps.filter(a => a.title.toLowerCase().includes(q)).slice(0, 7);
  }, [apps, query]);
  const go = (id: string) => {
    const app = apps.find(a => a.id === id);
    const path = `/apps/${id}`;
    recordSearch({ id, title: app?.title, query: (query || app?.title || '').trim(), path });
    router.push(path);
  };
  const onKeyDown: React.KeyboardEventHandler<HTMLInputElement> = (e) => {
    if (!filtered.length) return;
    if (e.key === 'ArrowDown') { e.preventDefault(); setActive(i => Math.min(i + 1, filtered.length - 1)); }
    else if (e.key === 'ArrowUp') { e.preventDefault(); setActive(i => Math.max(i - 1, 0)); }
    else if (e.key === 'Enter') { e.preventDefault(); go(filtered[Math.max(0, active)].id); }
  };

  const startVoice = () => {
    if (typeof window === 'undefined') return;
    const SR: any = (window as any).SpeechRecognition || (window as any).webkitSpeechRecognition;
    if (!SR) { alert('Voice recognition is not supported in this browser.'); return; }
    const rec = new SR();
    rec.lang = 'en-US';
    rec.interimResults = false;
    rec.maxAlternatives = 1;
    setListening(true);
    rec.onresult = (ev: any) => {
      const t = ev?.results?.[0]?.[0]?.transcript || '';
      const q = String(t).trim();
      setQuery(q);
      const match = findBestApp(q);
      if (match) go(match.id);
    };
    rec.onerror = () => setListening(false);
    rec.onend = () => setListening(false);
    rec.start();
  };

  return (
    <div className="flex-1 p-8">
      {/* Hero Section */}
      <div className="text-center mb-12">
        <h1 className="text-4xl font-bold text-gray-900 mb-4">
          One Ashley Super Agent
        </h1>
        <p className="text-lg text-gray-700 mb-8">
          Ask anything, create anything
        </p>
        
        {/* Main Search Bar */}
        <div className="max-w-2xl mx-auto mb-8">
          <div className="relative group">
            <input
              type="text"
              placeholder="Ask anything, create anything"
              value={query}
              onChange={(e) => { setQuery(e.target.value); setActive(0); }}
              onKeyDown={onKeyDown}
              className="w-full px-6 py-4 text-lg text-gray-900 placeholder-gray-500 bg-white/80 backdrop-blur border border-gray-200 rounded-2xl focus:outline-none focus:ring-2 focus:ring-[var(--ring)] focus:border-transparent shadow-sm hover:shadow-md focus:shadow-lg transition-all duration-300 placeholder:transition-opacity focus:placeholder-opacity-70"
            />
            {/* Icons inside input */}
            <div className="absolute right-4 top-1/2 transform -translate-y-1/2 flex items-center space-x-2">
              <button
                type="button"
                onClick={startVoice}
                aria-pressed={listening}
                className={`p-2 rounded-lg transition-all duration-200 ${listening ? 'text-[var(--ring)] bg-gray-100 animate-pulse' : 'text-gray-400 hover:text-gray-600 hover:bg-gray-100 hover:rotate-6 active:scale-95'}`}
              >
                <MicrophoneIcon className="w-5 h-5" />
              </button>
              <button className="p-2 rounded-lg text-gray-400 hover:text-gray-600 hover:bg-gray-100 transition-all duration-200 hover:rotate-3 active:scale-95">
                <PhotoIcon className="w-5 h-5" />
              </button>
            </div>

            {/* Suggestions dropdown */}
            {filtered.length > 0 && (
              <ul className="absolute left-0 right-0 mt-2 z-20 bg-white rounded-xl shadow-lg ring-1 ring-gray-200 overflow-hidden">
                {filtered.map((a, i) => (
                  <li key={a.id}>
                    <button
                      type="button"
                      className={`w-full text-left px-4 py-3 flex items-center gap-3 hover:bg-gray-50 ${i === active ? 'bg-gray-50' : ''}`}
                      onMouseDown={(e) => e.preventDefault()}
                      onClick={() => go(a.id)}
                    >
                      <div className="w-7 h-7 rounded-md brand-soft brand-text flex items-center justify-center text-xs font-semibold">
                        {a.title.split(' ').map(w => w[0]).join('').slice(0,3).toUpperCase()}
                      </div>
                      <span className="text-sm text-gray-900">{a.title}</span>
                    </button>
                  </li>
                ))}
              </ul>
            )}
          </div>
        </div>


      </div>

      {/* AI Agents Grid */}
      <div className="max-w-6xl mx-auto">
        <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-4 mb-8">
          {aiAgents.map((agent) => {
            const IconComponent = agent.icon;
            return (
              <div
                key={agent.id}
                className="relative bg-white rounded-xl p-4 border border-gray-200 hover:border-gray-300 hover:shadow-md transition-all cursor-pointer group"
              >
                {/* Badges */}
                {agent.isNew && (
                  <span className="absolute -top-2 -right-2 bg-green-500 text-white text-xs px-2 py-1 rounded-full">
                    NEW
                  </span>
                )}
                {agent.isHot && (
                  <span className="absolute -top-2 -right-2 bg-red-500 text-white text-xs px-2 py-1 rounded-full">
                    HOT
                  </span>
                )}
                
                <div className="w-12 h-12 rounded-lg brand-soft brand-text flex items-center justify-center mb-3">
                  <IconComponent className="w-6 h-6" />
                </div>
                <h3 className="font-medium text-gray-900 mb-1">{agent.name}</h3>
                <p className="text-sm text-gray-500">{agent.description}</p>
              </div>
            );
          })}
        </div>

        {/* For You Section - Feather fan on scroll */}
        <div className="mt-2">
          <ForYouFeather />
        </div>
      </div>
    </div>
  );
}
